.myWorksBlock {
    min-height: 100vh;
    /*background-color: #F1F1F1;*/
    background-color: #ffffff;
    display: flex;
    /*border-bottom: 6px solid #151313;*/
    padding: 20px 0;
}

.myWorksContainer {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: nowrap;
    height: auto;
}

.works {
    padding: 20px 0;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.title {
    /*background-color: powderblue;*/
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
}
